<template>
  <div class="home-view">
    <list-view v-if="isViewVisible('list')"/>
    <map-view v-if="isViewVisible('map')"/>
  </div>
</template>

<script>
import { useLayoutStore } from "@/stores/layout";

import { useDisplay } from 'vuetify';

import ListView from "@/components/home/ListView.vue";
import MapView from "@/components/home/map/MapView.vue";

export default {
  name: "HomeView",
  components: {
    ListView,
    MapView,
  },
  setup() {
    const layoutStore = useLayoutStore();
    const { smAndDown } = useDisplay()

    const isViewVisible = (view) => {
      if (!smAndDown.value) return true;

      return layoutStore.currentHomeView === view
    }

    return {
      isViewVisible
    }
  }
};
</script>

<style>
.home-view {
  display: flex;
  width: 100%;
  height: calc(100vh - 64px);
  overflow: hidden;
}
</style>
