<template>
  <div class="map-container d-flex align-center justify-center">
    <GoogleMap
      :api-key="apiKey"
      mapId="appMap"
      class="app-map"
      :center="mapCenter"
      :zoom="15"
      :map-type-control="false"
      :streetViewControl="false"
    >
      <AdvancedMarker
        v-for="marker in storeMarkers"
        :key="marker.id"
        :options="marker"
        :pinOptions="{ background: '#228B22', glyphColor: 'white' }"
        @click="showStoreDetails(marker)"
      />
    </GoogleMap>

    <map-details-dialog />
  </div>
</template>

<script>
import { computed } from "vue";

import storesMock from "@/mocks/storesMock";

import { useMapStore } from "@/stores/map";

import { GoogleMap, AdvancedMarker } from "vue3-google-map";

import MapDetailsDialog from "@/components/home/map/MapDetailsDialog.vue";

export default {
  name: "MapMain",
  components: { GoogleMap, AdvancedMarker, MapDetailsDialog },
  setup() {
    const mapStore = useMapStore();

    const apiKey = 'AIzaSyBoVy9a2U6SAzXmdFvbJe8Mq39M7QL9vKU';

    const mapCenter = {
      lat: 52.209427,
      lng: 21.070871,
    };

    const storeMarkers = computed(() => {
      return storesMock.map((store) => {
        return {
          id: store.id,
          position: store.location,
          title: store.name,
        };
      });
    });

    const showStoreDetails = (vendor) => {
      mapStore.setSelectedItem({
        id: vendor.id,
        type: "vendor",
      });
    };

    return {
      apiKey,
      mapCenter,
      storeMarkers,
      showStoreDetails,
    };
  },
};
</script>

<style>
.app-map {
  height: 100%;
  width: 100%;
}

.map-container {
  flex: 1;
  position: relative;
}
</style>
